$primaryColor: #4527a4;
$secondaryColor: #8344c5;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

@for $i from 1 through 12 {
	.columns-#{$i} {
		grid-template-columns: repeat(#{$i}, 1fr);
	}
}

@media (max-width: 768px) {
	@for $i from 1 through 12 {
		.columns-tablet-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

@media (max-width: 576px) {
	@for $i from 1 through 12 {
		.columns-mobile-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

@keyframes ifbShine {
	100% {
		left: 125%;
	}
}

// Instagram Feed
.wp-block-ifb-instagram {
	* {
		box-sizing: border-box;
	}

	.ifbInstagramFeed {
		.ifbGallery {
			display: grid;
		}

		.galleryHeader {
			display: flex;
			align-items: center;
			justify-content: space-between;

			.followBtn {
				margin-left: auto;
			}
		}

		.galleyFooter {
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 20px;
			margin-top: 20px;
		}

		.followBtn {
			text-decoration: none;
			display: inline-block;
			padding: 10px 20px;
			border-radius: 3px;
			transition: all 0.3s ease;
		}

		.loadMoreBtn {
			color: #fff;
			background: $primaryColor;
			display: inline-block;
			padding: 10px 20px;
			border: none;
			border-radius: 3px;
			transition: all 0.3s ease;
			cursor: pointer;

			&.hidden {
				display: none;
			}

			&:hover,
			&:focus {
				background-color: $secondaryColor;
				border: none;
			}
		}

		.galleryItem {
			height: 100%;
			position: relative;
			overflow: hidden;

			.typeIcon {
				position: absolute;
				top: 0;
				right: 0;
				padding: 8px;
				line-height: 50%;
			}

			.caption {
				p {
					width: max-content;
					max-width: 100%;
					padding: 0 15px;
				}
			}

			&:hover {
				figure {
					&.zoomIn {
						img {
							transform: scale(1.1);
						}
					}

					&.zoomOut {
						img {
							transform: scale(1);
						}
					}

					&.rotateIn {
						img {
							transform: scale(1.1) rotate(3deg);
						}
					}

					&.rotateOut {
						img {
							transform: scale(1) rotate(0deg);
						}
					}

					&.shine::before {
						animation: ifbShine .75s;
					}

					.caption {
						opacity: 1;
					}
				}
			}
		}

		figure {
			width: 100%;
			position: relative;
			padding-bottom: 100%;
			margin: 0;
			transition: all 0.4s ease-in-out;

			img {
				position: absolute;
				top: 0;
				left: 0;
			}

			&.zoomOut {
				img {
					transform: scale(1.1);
				}
			}

			&.rotateOut {
				img {
					transform: scale(1.1) rotate(3deg);
				}
			}

			&.shine {
				position: relative;

				&::before {
					content: '';
					display: block;
					position: absolute;
					top: 0;
					left: -75%;
					z-index: 2;
					width: 50%;
					height: 100%;
					background: linear-gradient(to right, #fff0 0%, #ffffff4d 100%);
					transform: skewX(-25deg);
				}
			}

			.caption {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				opacity: 0;
				margin: 0;
				transition: all 0.4s ease-in-out;

				p {
					position: absolute;
					margin: 0;
				}

				&.overlayTop p {
					top: 8%;
					left: 50%;
					transform: translate(-50%, 10%);
				}

				&.overlayCenter p {
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
				}

				&.overlayBottom p {
					bottom: 8%;
					left: 50%;
					transform: translate(-50%, 10%);
				}
			}
		}

		img,
		video {
			width: 100%;
			max-width: 100%;
			height: 100%;
			max-height: 100%;
			object-fit: cover;
			transition: all 0.3s cubic-bezier(0.20, 0.60, 0.70, 0.80);
		}
	}
}

.ifbProfile {
	display: flex;
	align-items: center;
	gap: 20px;

	.profileImg {
		min-width: 55px;
		width: 55px;
		height: 55px;
		overflow: hidden;
		box-sizing: border-box;
		margin: 0;
		border-radius: 50%;
	}

	.userName {
		font-size: 18px;
		font-weight: bold;
		line-height: 135%;
		text-decoration: none !important;
		display: inline-block;
		color: #4527a4;
		margin: 0;
	}

	.follow {
		font-size: 14px;
		font-weight: 600;
		line-height: 120%;
		display: flex;
		align-items: center;
		gap: 15px;
		margin: 10px 0 0 0 !important;

		* {
			margin: 0 !important;
		}
	}

	.name {
		font-size: 14px;
		font-weight: 700;
		line-height: 120%;
		margin: 15px 0 0 0 !important;
	}

	.biography {
		font-size: 14px;
		font-weight: 500;
		line-height: 120%;
		margin: 8px 0 0 0 !important;
	}
}

.ifbPopupContent {
	width: 1140px;
	max-width: calc(100% - 100px);
	display: none;
	padding: 0;
	border-radius: 5px;
	overflow: hidden;

	.contentArea {
		width: 100%;
		max-width: 100%;
		height: 100%;
		max-height: 100%;
		display: flex;
		overflow: auto;
		cursor: default;

		.media {
			flex: 1;
			width: calc(100% - 350px);

			img,
			video {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.text {
			min-width: 350px;
			max-width: 100%;
			padding: 15px 20px;

			.ifbProfile {
				padding-bottom: 15px;
				border-bottom: 1px solid lightgrey;
				margin-bottom: 20px;

				// .profileImg {
				// 	min-width: 40px;
				// 	width: 40px;
				// 	height: 40px;
				// }
			}

			.info {
				font-size: 14px;
				font-weight: bold;
				color: #333;
				margin-top: 25px;

				* {
					line-height: 100%;
					font-size: inherit;
					color: inherit;
				}

				.link {
					display: inline-flex;
					align-items: center;
					gap: 4px;
				}
			}
		}

		@media (max-width: 992px) {
			.media {
				width: calc(100% - 300px);
			}

			.text {
				min-width: 300px;
			}
		}

		@media (max-width: 768px) {
			flex-direction: column;

			.media {
				width: 100%;
				height: 0;
				position: relative;
				padding-bottom: 100%;

				>* {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
				}
			}
		}
	}
}

.ifbCarousel {
