.ifbLoading {
	font-weight: 700;
	padding: 0;
	margin: 0;
}

.galleryLoading {
	height: 520px;
	overflow: hidden;
	background: #000;
	position: relative;

	// Animation
	animation: galleryAnimate 2.8s linear infinite forwards;
	background: #f6f7f8;
	background: linear-gradient(to right, #eee 2%, #ddd 18%, #eee 33%);
	background-size: 1820px;

	&Item {
		background: #fff;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;

		&:nth-child(1) {
			left: 55px;
			width: 15px;
			height: 55px;
		}

		&:nth-child(2) {
			left: calc(55px + 15px);
			width: calc(100% - (55px + 15px + 180px));
			height: 16.5px;
		}

		&:nth-child(3) {
			top: calc(22px + 16.5px);
			left: calc(55px + 15px);
			width: calc(100% - (55px + 15px + 180px));
			height: 17px;
		}

		&:nth-child(4) {
			left: calc(55px + 15px + 150px);
			width: calc(100% - (55px + 15px + 150px + 180px));
			height: 55px;
		}

		&:nth-child(5) {
			left: calc(100% - 180px);
			width: 180px;
			height: 7.5px;
		}

		&:nth-child(6) {
			top: calc(40px + 7.5px);
			left: calc(100% - 180px);
			width: 180px;
			height: 7.5px;
		}

		&:nth-child(7) {
			top: 55px;
			width: 100%;
			height: 15px;
		}

		&:nth-child(8),
		&:nth-child(9),
		&:nth-child(10) {
			top: 70px;
			width: 15px;
			height: 660px;
		}

		&:nth-child(8) {
			left: calc(25% - 7.5px);
		}

		&:nth-child(9) {
			left: calc(50% - 7.5px);
		}

		&:nth-child(10) {
			left: calc(75% - 7.5px);
		}

		&:nth-child(11) {
			top: 280px;
			width: 100%;
			height: 15px;
		}
	}
}

@keyframes galleryAnimate {
	0% {
		background-position: -910px 0;
	}

	100% {
		background-position: 910px 0;
	}
}